.home-content {
  padding: 24px 24px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
}
.home-content__select-price {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
}
.home-content__select-price__label {
  font-size: 20px;
  margin-right: 12px;
}
.home-content__pizzas {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.home-content__inline {
  flex-flow: column;
}
.home-content__inline .home-content__select-price {
  justify-content: flex-start;
}
.home-content__inline .home-content__pizzas {
  flex-flow: column nowrap;
}
