.delivery-history-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #d0d7dd;
  padding: 16px;
  margin-bottom: 16px;
}
.delivery-history-item:last-child {
  margin-bottom: 0;
}
.delivery-history-item__info__element {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}
.delivery-history-item__info__element__label {
  font-size: 20px;
  margin-right: 8px;
  color: rgba(0, 0, 0, 0.85);
}
.delivery-history-item__info__element__data {
  font-size: 18px;
}
.delivery-history-item__info__element__items__label {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.85);
}
.delivery-history-item__info__element__items__data__element > span {
  margin-right: 8px;
}
.delivery-history-item__info__element__items__data__element > span:last-child {
  margin-right: 0;
}
