.pizza-element {
  width: 240px;
  margin-right: 48px;
  margin-bottom: 48px;
  display: flex;
  flex-flow: column;
}
.pizza-element .ant-card-cover {
  padding: 8px;
  display: flex;
  align-items: center;
}
.pizza-element .ant-card-body {
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: flex-start;
}
.pizza-element .ant-card-body:before,
.pizza-element .ant-card-body:after {
  display: none;
}
.pizza-element__bottom {
  margin-top: 16px;
  width: 100%;
}
.pizza-element__bottom__element {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.pizza-element__bottom__element__price {
  color: rgba(0, 0, 0, 0.65);
  font-size: 20px;
}
.pizza-element__bottom__element__add .ant-input-number {
  width: 50px;
}
.pizza-element__bottom__element__add .ant-input-number .ant-input-number-handler-wrap {
  opacity: 1;
}
.pizza-element__bottom__element__add .ant-input-number .ant-input-number-handler-wrap span:hover .anticon:hover {
  color: #1890ff;
}
.pizza-element__bottom__element__add .ant-input-number input {
  padding: 0 8px;
}
.pizza-element__bottom__element__add .ant-input-number:hover {
  border-color: #1890ff;
}
.pizza-element__bottom__element__total {
  color: rgba(0, 0, 0, 0.65);
  font-size: 20px;
}
.pizza-element__inline {
  width: 100%;
  flex-flow: row nowrap;
  margin-bottom: 16px;
}
.pizza-element__inline .ant-card-cover > * {
  height: 220px;
  width: 220px;
}
