.header {
  padding: 0 32px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 64px;
  height: 100%;
}
.header__right-side {
  display: flex;
  flex-flow: row wrap;
}
.header__logo {
  font-size: 32px;
  color: rgba(255, 255, 255, 0.85);
  font-weight: bold;
  cursor: pointer;
}
.header__total {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  color: rgba(255, 255, 255, 0.85);
  font-size: 24px;
  margin-right: 112.8px;
  line-height: 24px;
}
.header__total__usd {
  margin-left: 24px;
}
.header__total__cart {
  margin-left: 12px;
}
.header__total__cart:disabled > * {
  color: #d0d7dd;
}
.header__auth {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.header__auth__user {
  display: flex;
  flex-flow: row nowrap;
  cursor: pointer;
  align-items: center;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.85);
  margin-right: 16px;
}
.header__auth__user__email {
  margin-left: 8px;
}
