.cart-content__total {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  align-items: center;
}
.cart-content__total__info {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
}
.cart-content__total__info__label {
  font-size: 26px;
  margin-right: 16px;
}
.cart-content__total__info__prices {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 26px;
}
